import React from "react";
import { Col, Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import Fade from "react-reveal/Fade";

//Component pour l'affichage des motos dans des cards
function Trip(props) {
  return (
    <Col sm={4} className="mt-5">
      <Fade left cascade>
        <Card style={{ width: "28rem" }}>
          <Card.Img variant="top" src={props.picture1} />
          <Card.Body>
            <Card.Title>
              <Link to={"trip/" + props.nameTrip + "?id=" + props.id}>
                <h2>{props.nameTrip}</h2>
              </Link>
            </Card.Title>
            <Card.Text>
              <h3>{props.descriptionTrip}</h3>
            </Card.Text>
            <Card.Text>
              <h3>{props.regionTrip}</h3>
            </Card.Text>
            <Card.Text>
              {Object.keys(props.attraitsTrip).map((key) => (
                <p>{props.attraitsTrip[key].nom_attrait}</p>
              ))}
            </Card.Text>
          </Card.Body>
        </Card>
      </Fade>
    </Col>
  );
}
export default Trip;
